import axios from 'axios';
import {
  SearchConditionInterface,
  ResponsePageDataInterface,
  ResponseDataInterface,
} from './commonTypes';

export interface UserInterface {
  idEmail: String;
  // 用户姓名
  username: String;
  // 用户密码
  password: Number;
  // 用户标签
  tags: String[];
}
axios.defaults.withCredentials = true;
export class UserService {
  public static async getUsers(
    condition: SearchConditionInterface
  ): Promise<ResponsePageDataInterface<UserInterface>> {
    const resp = await axios.post('/api/login', condition);
    return resp.data;
  }

  public static async clearUsers(): Promise<void> {
    const resp = await axios.post('/api/login/out');
    console.log(document.cookie);
    return resp.data;
  }

  public static async whoAmI(): Promise<
    ResponseDataInterface<UserInterface | null>
  > {
    const resp = await axios.get('/api/login/user');
    console.log(resp);
    return resp.data;
  }
}
